import React from "react";
import styled from "styled-components";

const StyledOverlay = styled.div`
	position: absolute;
	width: ${(props) => (props.visible ? 100 : 0)}%;
	height: ${(props) => (props.visible ? 1000 : 0)}%;
	top: 0;
	left: 0;

	& div {
		background-color: black;
		width: 100vw;
		height: 25vh;
		transition: transform 1s;
		transform: translateX(${(props) => (props.visible ? 0 : -120)}vw);
	}

	& #uno {
		transition-delay: 0s;
	}

	& #dos {
		transition-delay: 0.2s;
	}

	& #tres {
		transition-delay: 0.4s;
	}

	& #cuatro {
		transition-delay: 0.6s;
	}
`;

const StyledButton = styled.button`
	position: absolute;
	background-color: black;
	color: white;
	padding: 1em;
	top: 1rem;
	left: 1rem;
	cursor: pointer;
`;

function Overlay({ visible, setVisible }) {
	return (
		<StyledOverlay visible={visible}>
			<div id="uno"></div>
			<div id="dos"></div>
			<div id="tres"></div>
			<div id="cuatro"></div>
			{/* <StyledButton onClick={() => setVisible((valor) => !valor)}>
				a
			</StyledButton> */}
		</StyledOverlay>
	);
}

export default Overlay;
